.c-section__text-and-image + .c-section__image-and-text-double,
.c-section__science-ingredients + .c-section__before-and-after-slider
{
  &::before {
    display: block;
    content: '';
    width: 100%;
    width: calc(100% - 80px);
    margin: auto;
    border-top: 1px solid rgba($s-color--aqua, 0.3);
  }

  @include t-media-query($s-medium-up) {
    &::before {
      display: none;
    }
  }
}