.c-main-menu {
  z-index: $s-z--menu;

  //Shared styles for desktop and mobile
  &__list {
    margin: 0;
    list-style: none;
    padding: 0
  }

  //Background close element
  &__background {
    position: fixed;
    z-index: $s-z--menu-overlay;
    height: 100%;
    top: 0;
    left: 0;
    width: 0;
    opacity: 0;
    background: $s-color--background-quaternary;
    transition: opacity $s-anim--time-med $s-anim--ease-in-out,
      width 0s $s-anim--ease-in-out $s-anim--time-med;

    .is-menu-open &,
    .is-menu-hover & {
      width: 100%;
      opacity: 0.6;
      transition: opacity $s-anim--time-med $s-anim--ease-in-out 0.1s,
        width 0s $s-anim--ease-in-out;
    }
  }

  /*==== Media Queries ====*/
  //Mobile menu specific styles
  @media only screen and (max-width: 1099px) {
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    width: 100%;
    max-width: t-to-rem(375);
    overflow: hidden;
    background: $s-color--background;
    @include t-anim--transition(left, $s-anim--time-med);

    &__header {
      border-bottom: 1px solid rgba($s-color--background-secondary, .1);
      min-height: t-to-rem(72);
      position: relative;

      &-account {
        position: absolute;
        left: t-to-rem(37);
        top: t-to-rem(24);
        cursor: pointer;

        &-icon {
          @include t-width-and-height(25);
        }

        svg path {
          stroke: $s-color--background-secondary;
        }
      }

      &-close {
        position: absolute;
        right: t-to-rem(37);
        top: t-to-rem(24);
        @include t-width-and-height(12);
        cursor: pointer;

        svg path {
          stroke: $s-color--background-secondary;
        }
      }

      &-item {
        height: 100%;
      }

      &-link {
        display: flex;
        align-items: center;
        height: 100%;
        padding-left: t-to-rem(37);

        &-icon {
          @include t-width-and-height(14);
          transform: rotate(180deg);
          margin-right: t-to-rem(20);

          svg {
            height: 100%;

            path {
              stroke: $s-color--font-1;
            }
          }
        }
      }
    }

    &.is-menu-open {
      left: 0;
    }

    //UL List
    &__list {
      position: absolute;
      width: 100%;
      top: 0;
      bottom: 0;
      left: 0;

      &.is-level-1 {
        display: flex;
        flex-direction: column;
        left: 0;
        @include t-anim--transition(left, $s-anim--time-med);

        //Menu level states
        .is-menu-level-2 & {
          left: -100%;
        }

        .is-menu-level-3 & {
          left: -200%;
        }

        .is-menu-level-4 & {
          left: -300%;
        }
      }

      &.is-level-2,
      &.is-level-3 {
        left: 100%;
      }

      //Styles for each level at different menu level states
      &.is-level-1 {

        &.is-submenu-open {
          .is-menu-level-1 & {
            overflow-x: hidden;
            overflow-y: auto;
          }
        }
      }

      &.is-level-2 {
        opacity: 0;

        .is-menu-level-1 & {
          overflow: hidden;
          opacity: 0;
        }

        &.is-submenu-open {
          .is-menu-level-2 & {
            opacity: 1;
            overflow-x: hidden;
            overflow-y: auto;
          }
        }
      }

      &.is-level-3 {
        opacity: 0;

        .is-menu-level-2 & {
          overflow: hidden;
          opacity: 0;
        }

        &.is-submenu-open {
          opacity: 1;
          z-index: 2;

          .is-menu-level-3 & {
            overflow-x: hidden;
            overflow-y: auto;
          }
        }
      }

      //Shared submenu open styles
      &.is-submenu-open {
        opacity: 1;
        z-index: 2;
      }

      &-container {
        position: relative;
        height: calc(100% - 4.5rem);

        //Gradient element
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 12rem;
          pointer-events: none;
          background: linear-gradient(180deg, rgba($s-color--background, 0), rgba($s-color--background, 0) 5%, rgba($s-color--background, 1));
          z-index: 2;
          opacity: 0;

          .is-level-2 & {
            opacity: 1;
          }
        }
      }
    }

    //LI element 
    &__item {
      &:first-of-type {
        margin-top: t-to-rem(20);
      }

      .is-level-2 & {
        margin: 0;

        &.is-back {
          background-color: white;
          color: $s-color--font-1;
          border-bottom: 1px solid rgba($s-color--background-secondary, .1);
          height: t-to-rem(72);
        }
      }

      &.has-submenu:last-child {
        margin-bottom: 6rem;
      }

      &.is-account {
        margin-top: auto;
        margin-bottom: t-to-rem(35);

      }
    }

    //Individual link
    &__link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: t-to-rem(20) t-to-rem(40);
      color: $s-color--font-1;

      &:not(.is-back) {
        font-size: t-to-rem(32);
      }

      .is-account & {
        font-size: t-to-rem(22);
        font-weight: $s-font--weight-normal;
      }

      &-icon {

        &-submenu {
          background-image: url('../../assets/icon-chevron.svg');
          width: t-to-rem(14);
          height: t-to-rem(14);
          position: relative;
          top: 0.25rem;
        }

        &-back {
          background-image: url('../../assets/icon-chevron.svg');
          width: t-to-rem(14);
          height: t-to-rem(14);
          transform: rotate(180deg);
        }

        &-close {
          background-image: url('../../assets/icon-close.svg');
          width: t-to-rem(36);
          height: t-to-rem(36);
          background-size: t-to-rem(12);
          background-position: center center;
          background-repeat: no-repeat;
          border-radius: 100%;
        }

        &-arrow {
          display: none;
        }
      }

      &[href="/account"] {
        margin-top: auto;
      }

      &-inner {
        .is-active & {
          border-bottom: 1px solid;
        }
      }
    }
  }

  //Desktop menu specific styles
  @include t-media-query($s-medium-up) {
    flex: 1;
    height: 100%;

    &__header {
      display: none;
    }

    //UL Element of top level links
    &__list {
      display: flex;
      justify-content: flex-start;
      align-items: stretch;
      padding: 0;
      color: $s-color--font-1;

      //Container outside the links in the top level of the menu
      &-container {
        display: flex;
        height: 100%;

        .c-header__right & {
          justify-content: flex-end;
          margin-right: t-to-rem(100);
        }
      }

      &.is-level-1 {
        height: 100%;

        .c-main-menu__link {
          .has-inverted-header & {
            color: $s-color--font-6;
          }
        }
      }

      //Level 2 - mega nav for desktop users
      &.is-level-2 {
        position: absolute;
        width: 100%;
        left: 0;
        bottom: auto;
        top: 100%;
        max-height: 0;
        align-items: stretch;
        padding: 0;
        opacity: 0;
        @include t-anim--transition(max-height opacity, $s-anim--time-med-slow);
        justify-content: space-between;
        max-height: 0;

        &:before {
          content: '';
          position: absolute;
          top: t-to-rem(-164);
          left: 0;
          width: 100%;
          height: 0;
          opacity: 0;
          @include t-anim--transition(opacity, $s-anim--time-med);
          background: $s-color--background-tertiary;
          z-index: -1;
        }

        &:after {
          content: '';
          position: absolute;
          top: 100%;
          left: 0;
          width: 100%;
          height: 0;
          @include t-anim--transition(opacity, $s-anim--time-med);
          background: linear-gradient(rgba($s-color--background-quaternary, .7), rgba($s-color--background-quaternary, 0));
        }

        .c-nav-product {
          display: none;
        }
      }
    }

    //Individual list item
    &__item {
      display: flex;
      align-items: stretch;

      &.is-back {
        display: none;
      }

      &.is-additional {
        display: none;
      }

      .is-level-1 & {
        margin-right: t-to-rem(16);

        &:first-of-type {
          padding-right: t-to-rem(16);
          margin-right: 0;
        }
      }

      //Additional mobile menu links
      &.is-additional {
        display: none;
      }

      &:hover {
        &>.c-main-menu {

          //Submenu expand/hover effect
          &__list.is-level-2 {
            max-height: t-to-rem(877);
            opacity: 1;
            background: $s-color--background;
            border-bottom: 1px solid rgba($s-color--background-secondary, 0.2);
            visibility: visible;

            //Pseudo element used for the upper shadow
            &:before {
              opacity: 1;
              height: t-to-rem(164);
            }

            &:after {
              opacity: 1;
              height: t-to-rem(200);
            }

            .c-nav-product.is-desktop {
              display: flex;
            }
          }

          &__link {
            text-decoration: none;
          }
        }
      }
    }

    &__link {
      display: flex;
      align-items: center;
      position: relative;
      z-index: 1;
      color: $s-color--font-1;

      &:hover {
        color: $s-color--font-6 !important;

        .has-inverted-header & {
          color: $s-color--font-1 !important;
        }
      }

      //Pseudo element used for underline effect on top levels
      &-inner {
        position: relative;
        white-space: nowrap;
      }

      &-icon-arrow {
        width: 10px;
        height: 7px;
        margin-left: 3px;

        path {
          stroke: $s-color--font-1;
        }
      }

      //Mobile accordion style icons
      &-icon {
        display: none;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__item {
      .is-level-1 & {
        margin-right: t-to-rem(32);

        &:first-of-type {
          padding-right: t-to-rem(32);
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__item {
      .is-level-1 & {
        margin-right: t-to-rem(40);

        &:first-of-type {
          padding-right: t-to-rem(40);
        }
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__item {
      .is-level-1 & {
        margin-right: t-to-rem(64);

        &:first-of-type {
          padding-right: t-to-rem(64);
        }
      }
    }
  }
}