.o-btn {
  @extend %s-btn--base;
  @include t-anim--transition(all, $s-anim--time-med);

  &.is-primary {
    @extend %s-btn--primary;
  }

  &.is-secondary {
    @extend %s-btn--secondary;
  }

  &.is-tertiary {
    @extend %s-btn--tertiary;
  }

  &.is-quaternary {
    @extend %s-btn--quaternary;
  }

  &.is-disabled,&[disabled="disabled"] {
    @extend %s-btn--disabled;
  }

}
