@keyframes o-loader--spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.o-loader {
  &__container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba($s-color--white, 0.5);
    z-index: $s-z--loader;

    .o-loader {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  svg {
    @include t-anim--animation('o-loader--spin', $time: $s-anim--time-med, $ease: linear);
  }
}