
.o-link {
  color: inherit;
  @include t-font--type-2;
  position: relative;
  display: inline-block;

  &::before{
    content: "";
    height: 2px;
    background-color: $s-color--font-1;
    width: 0;
    position: absolute;
    top: 100%;
    margin-top: t-to-rem(4);
    left: 0;
    transition: width 1s;
  }
  
  &:hover {
    text-decoration: none;
    &::before{
      width: 100%;
    }
  }

  &.is-block {display: block;}

  &.has-arrow{
    background: url('../../assets/icon-chevron.svg')  no-repeat center right;
    padding-right: t-to-rem(16);
    background-size: t-to-rem(11);
    background-position-y: t-to-rem(8);
  }

  @include t-media-query($s-medium-up) {
    &.has-arrow{
      padding-right: t-to-rem(18);
      background-size: t-to-rem(12);
      background-position-y: t-to-rem(8);
    }
  }
  @include t-media-query($s-xlarge-up) {
    &.has-arrow{
      padding-right: t-to-rem(22);
      background-size: t-to-rem(13);
      background-position-y: t-to-rem(9);
    }
  }
}
