.o-afterpay {
  margin: 1.563rem 0 0;

  &__text {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
  }

  &__link {
    // width: 1.125rem;
    // height: 1.125rem;
    cursor: pointer;
    margin-left: 0.625rem;
  }

  &__svg {
    width: 100%;
    height: auto;
  }

  &__popup {
    background-color: $s-color--background;
    padding: 2rem;
    max-width: 33rem;
    overflow: auto;

    ul,
    ol {
      padding-left: 2rem;
      list-style-type: circle;
      margin-bottom: 1rem;
    }

    li {
      @include t-font--type-base;
      color: $s-color--font-8;
    }
  }

  @include t-media-query($s-medium-up) {
    margin: 2.063rem 0 0;
  }
}