.o-swatches {

  &__swatch-size {
    margin-right: t-to-rem(26);
    font-size: t-to-rem(16);
    color: $s-color--font-6;

    &:last-of-type {
      margin-right: unset;
    }

    &-inner {
      display: flex;
      align-items: center;
    }
  }

  &__selected-circle {
    @include t-width-and-height(24);
    margin-right: t-to-rem(10);
    border-radius: 100%;
    border: 1px solid $s-color--background-secondary;
    position: relative;

    &-inner {
      opacity: 0;
      transition: opacity $s-anim--time-short;
      @include t-width-and-height(15);
      @include t-absolute-center-x-y;
      border-radius: 100%;
      background-color: $s-color--background-secondary;

      .is-selected & {
        opacity: 1;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__swatch-size {
      font-size: t-to-rem(18);
    }
  }

  @include t-media-query($s-large-up) {
    &__selected-circle {
      @include t-width-and-height(32);
      margin-right: t-to-rem(10);


      &-inner {

        @include t-width-and-height(20);



      }
    }
  }
}