/*
 *  Quantity Selector
 *    Styles for quantity Selector
 *
 *  Version:
 *    1.0.0 - 2019/05/09
 */

.o-quantity-selector {

  &__inner {
    display: flex;
    height: 100%;
    width: 112px;
    align-items: center;
    margin-left: auto;
  }

  &__button {
    background-color: $s-color--background;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    border: none;
    position: relative;
    border: 1px solid;

    &.is-plus {
      // border-top-left-radius: 0;
      // border-bottom-left-radius: 0;
      // background: transparent;
    }

    &.is-minus {
      // border-top-right-radius: 0;
      // border-bottom-right-radius: 0;
      // background: transparent;
    }

    //Hover State
    &:hover {
      background-color: $s-color--background;
    }

    &.is-disabled,
    &.is-disabled:hover {
      background-color: $s-color--font-8;

      svg {
        opacity: 0.4;
      }
    }

    svg {
      @include t-absolute-center-x-y;
      width: t-to-rem(7);
    }
  }

  &__input {
    flex: 1;

    &-field {
      padding: 0;
      @include t-font--type-1;
      -moz-appearance: textfield;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }
  }

  &__input-field.o-input {
    width: 100%;
    height: 100%;
    text-align: center;
    border: 0;
    background: transparent;
    border-radius: 0;
    padding: 0;
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-medium-up) {

    &__input-field {
      font-size: 1.125em;
    }
  }

  @include t-media-query($s-large-up) {
    &__inner {
      width: 197px;
    }

    &__button {
      height: 32px;
      width: 32px;
    }
  }
}