.c-main-menu__list.is-community-submenu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2.5rem;
  box-shadow: 0px 17px 20px rgba(0, 0, 0, 0.1);

  &.is-level-2 {
    top: 71%;
    left: unset;
    background: unset;
    border-bottom: unset;
    width: auto;
    overflow: hidden;

    &:before {
      background: unset;
    }

    &:after {
      background: unset;
    }
  }

  .c-community-submenu__link {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;

    &:not(:first-child) {
      margin-bottom: 2rem;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.is-unlocked {
      .c-community-submenu__link-icon {
        display: none;
      }

      .c-community-submenu__link-title {
        color: $s-color--font-1;
      }
    }

    &.is-active {
      &.c-community-submenu__link-icon {
        display: none;
      }
    }

    &-title {
      @include t-font--type-6;
      color: $s-color--font-2;
    }

    &-icon {
      margin-left: 10px;
      width: 22px;
      height: 22px;
    }
  }

  /*==== Media Queries ====*/
  //Mobile menu specific styles
  @include t-media-query($s-medium-down) {
    &.is-level-2 {
      left: 100%;
      top: 0;
      padding: 0;
      width: 100%;

      .c-main-menu__item {
        width: 100%;
      }

      .c-community-submenu__link {
        width: 100%;
        padding: 0 2.5rem;
        justify-content: flex-start;

        &:not(:first-child) {
          margin-top: 2.5rem;
        }

        &-title {
          font-size: 1rem;
        }
      }
    }
  }
}