.o-cart-quantity {
  display: inline-flex;
  align-items: center;


  &__button {
    background: $s-color--aqua;
    width: 2.25rem;
    height: 2.25rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all $s-anim--time-short $s-anim--ease-out;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 0;

    &:hover {
      background-color: $s-color--light-aqua;
    }

    svg, svg * {
      fill: $s-color--white;
      width: 0.5rem;
      height: 0.5rem;
    }
  }

  &__number {
    width: 2.875rem;
    text-align: center;

    -moz-appearance: textfield;
    &::-webkit-outer-spin-button,&::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  @include t-media-query($s-medium-up) {
    &__button {
      //width: 3rem;
      //height: 3rem;

      svg,svg * { width: 0.75rem; height: 0.75rem; }
    }

    &__number {
      width: 3.875rem;
    }
  }
}