/*
 *  Pagination
 *    Standard pagination. A fair bit of trumping due to how Shopify injects the facets.
 *
 * Dependencies:
 *
 *  Version:
 *    1.0.0 - 2021/04/26
 */

 .o-pagination {
  width: 100%;
  text-align: center;
  letter-spacing: 0;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  margin-bottom: t-to-rem(80);

  > span {
    display: inline-block;


    &.page {
      color: $s-color--font-8;
      padding: t-to-rem(18);
      &:last-child{
        margin-right: unset;
      }

      &.current {
        cursor: default;
        color: $s-color--font-1;
      }

      & a {
        display: inline-block;
        &:hover {
          color: $s-color--font-1;
        }
      }
    }

    &.next,
    &.prev {
      font-size: 0;
      @include t-width-and-height(42);
      background: $s-color--background-tertiary url('../../assets/icon-arrow.svg') no-repeat center;
      border-radius: 100%;
      display: block;
      background-size: 0.8125rem;
      cursor: pointer;
      overflow: hidden;
      a{
        display: inline-block;
        width: 100%;
        height: 100%;
      }
      &:hover{
        background-color: $s-color--light-aqua;
      }
    }
    &.next{
      margin-left: t-to-rem(8);
    }
    &.prev{
      transform: rotate(180deg);
      margin-right: t-to-rem(8);
    }
  }

  @include t-media-query($s-medium-up) {
    margin-bottom: t-to-rem(170);
    
    > span {
      &.next,
      &.prev {
        @include t-width-and-height(50);
      }
      &.next{
        margin-left: t-to-rem(12);
      }
      &.prev{
        margin-right: t-to-rem(12);
      }

      
      &.page {
        color: $s-color--font-8;
        padding: t-to-rem(22);
      }
    }

  }
}
