html {
  font-size: $s-font--root-size;
}

body {
  @include t-font--type-base;
}

html,
body {
  margin: 0;
  padding: 0;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  background-color: $s-color--background;
  scroll-behavior: smooth;

  &.is-scroll-disabled {
    overflow: hidden;
  }

  /**
    Accessibility focus styles - replace border variable with correct
    border color
  */
  .js-focus-visible :focus:not(.focus-visible) {
    outline: none;
  }

  *:focus {
    outline: $s-color--border-focus solid 0.0625rem;
  }

  *:focus:not(:focus-visible) {
    outline: none;
  }

  *:focus-visible {
    outline: $s-color--border-focus solid 0.0625rem;
  }

  .js-focus-visible :focus:not(.focus-visible) {
    outline: none;
  }

  .o-main__wrapper {
    overflow-x: hidden;

    &.is-wrap-product {
      overflow: hidden;
    }

    &.is-policy {
      .shopify-policy__container {
        margin: 8rem auto 4rem;

        @include t-media-query($s-medium-up) {
          margin: 12rem auto 6rem;
        }

        .shopify-policy__title {
          margin-bottom: 2rem;
        }

        .rte {

          h1,
          h2,
          h3,
          h4,
          h5 {
            @include t-font--heading-5;
            margin-bottom: 2.0625rem;
            padding-top: 2.25rem;
          }

          ul,
          ol {
            padding-left: 2rem;
            list-style-type: disc;
            margin-bottom: 2rem;
          }

          li {
            @include t-font--type-base;
            color: $s-color--font-8;
          }
        }

        p {
          margin-bottom: 2rem;
        }
      }
    }
  }

  &.c-page--cart {
    .o-main__wrapper {
      overflow-x: visible;
    }
  }
}


.with-ordered-child {
  h3 {
    padding: 0 !important;
    margin-bottom: 10px !important;
  }
  ol.list-type-a {
    list-style-type: lower-alpha;
  }
  ol.list-type-i {
    list-style-type: lower-roman;
  }
  ol.list-type-A {
    list-style-type: upper-alpha;
  }
  ol:not(.list-type-a):not(.list-type-i):not(.list-type-A) {
    list-style-type: none;
    counter-reset: item;
    margin: 0;
    padding: 0;

    & > li {
      display: table;
      counter-increment: item;
      margin-bottom: 0.6em;
    }

    & > li:before {
      content: counters(item, ".") ". ";
      display: table-cell;
      padding-right: 0.6em;
    }

    li ol:not(.list-type-a):not(.list-type-i):not(.list-type-A) > li:before {
      content: counters(item, ".") " ";
    }
  }

  li {
    margin: 15px 0;
  }
}