/*
 *  Read More
 *    Read More object, used for toggling content.
 *
 *
 *  Version:
 *    1.0.0 - 2020/11/28
 */

.o-read-more {
  position: relative;
  overflow: hidden;

  //Content Toggle
  &__content {

    &.is-full {
      position: absolute;
      z-index: -1;
      opacity: 0;

      .is-open & {
        position: static;
        opacity: 1;
      }
    }

    &.is-less {
      opacity: 1;

      .is-open & {
        position: absolute;
        z-index: -1;
        opacity: 0;
      }
    }
  }

  //Toggle Buttons
  &__btn {
    text-decoration: none;
    display: inline-block;
    margin-top: 1rem;
    font-size: 0.875rem;

    &.is-more {
      .is-open & {
        display: none;
      }
    }

    &.is-less {
      display: none;

      .is-open & {
        display: inline-block;
      }
    }
  }


  /*==== Media Queries ====*/


  @include t-media-query($s-medium-up) {

    
  }

  @include t-media-query($s-xlarge-up) {

    
  }
}