.o-input {
  // Fix iOS Shadows
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  // Fix webkit autofill
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
    box-shadow: 0 0 0 100vw $s-color--background inset !important;
  }

  @include t-font--type-2;
  color: $s-color--font-6;
  padding: t-to-rem(20) 0;
  border-bottom: 1px solid rgba($s-color--background, .2);

  // Display Related
  position: relative;
  display: inline-block;
  width: 100%;

  // Typography

  &::placeholder {
    @include t-font--heading-7;
    color: $s-color--font-6;
  }

  resize: none;

  &:focus-visible {
    outline: none;
    // border-color: darken($s-color--background, 8%);
  }

  * + & {
    margin-top: 0.625rem;
  }

  &.is-select {
    color: inherit;
    background-repeat: no-repeat;
    background-size: 0.75rem 0.75rem;
    background-position: right 1.25rem center;

    &::placeholder {
      color: inherit;
    }
  }

  &.has-error {
    border-color: $s-color--error;

    &:focus {
      // border-color: $s-color--input-border;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {

  }

  @include t-media-query($s-xlarge-up) {

  }
}