.c-nav-product{

  &.is-desktop{display: none;}
  &__cta{
    @extend %s-btn--primary;
    min-width: t-to-rem(112);
    max-width: t-to-rem(171);
    margin: 0 auto  t-to-rem(26);
  }

  &__title {
    position: relative;
    z-index: 60;
  }

  
  @include t-media-query($s-medium-down) {
    &__inner{
      margin: t-to-rem(60) t-to-rem(40);
      display: flex;
      align-items: flex-start;
    }
    &__image{
      max-height: t-to-rem(141);
      width: auto;
      max-width: t-to-rem(92);

      &-container{
        padding: 0 t-to-rem(24) t-to-rem(14);
        width: t-to-rem(92);
        margin-right: t-to-rem(20);
        display: flex;
        justify-content: center;
        position: relative;
      }

      &-background{
        width: 100%;
        height: t-to-rem(77);
        position: absolute;
        bottom: 0;
        z-index: -1;
        background-size: cover;

        &-hover {
          display: none;
        }
      }
    }
    &__content{
      flex: 1;
    }

    &__right-arrow{
      display: flex;
      @include t-width-and-height(36);
      background-color: $s-color--background-tertiary;
      border-radius: 100%;
      padding: t-to-rem(12);
      padding-left: t-to-rem(14);
      justify-content: center;
      align-items: center;
    }
    &__title{
      margin-bottom: t-to-rem(12);
    }

  }

  @include t-media-query($s-medium-up) {
    &.is-desktop{display: block;}
    &.is-mobile{display: none;}

    text-align: center;
    justify-content: stretch;
    border-right: 1px solid rgba($s-color--background-secondary, 0.2);
    position: relative;
    z-index: 0;
    flex: 1;
  
    &__inner{
      max-width: t-to-rem(420);
      padding: t-to-rem(45) t-to-rem(38) t-to-rem(50);
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
  
  
    }
  
    &:last-child{
      border-right: unset;
    }

    &__image-background-hover {
      @include t-width-and-height(190);
      border-radius: 100%;
      //margin: t-to-rem(-66) auto 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      opacity: 0;
      transition: opacity $s-anim--time-med $s-anim--time-med;
    }

    &__image {
      position: relative;
    }
    
    &__hover-background{
      @include t-width-and-height(190);
      border-radius: 100%;
      margin: t-to-rem(-66) auto 0;

      &-container {
        position: absolute;
        bottom: 0;
        width: 100%;
        background: $s-color--background-tertiary;
        height: 0;
        overflow: hidden;
        z-index: -1;
        display: flex;
        justify-content: center;
        transition: height $s-anim--time-med;
      }
    }

    &:hover{
      .c-nav-product{
        
        &__hover-background-container{
          height: 60%;
        }

        &__image-background-hover {
          opacity: 1;
        }
      }
    }
  
    &__wrapper{
      position: relative;
      overflow: visible;
    }
  
    &__title{
      margin-bottom: t-to-rem(20);
      margin-top: auto;
  
    }
  
    &__image {
      height: t-to-rem(164);
      width: auto;
      margin: 0 auto t-to-rem(26);
      &-container{
        margin-top: auto;
        position: relative;
      }
    }
    &__text{      
      color: $s-color--font-1;
    }
  
    
    .is-middle-product &{
      background-color: $s-color--background;
      position: relative;
      transition: top $s-anim--time-short $s-anim--ease-out;
      top: 0;
  
      &-title{
        color: $s-color--font-1;
      }
    }
  }
  @include t-media-query($s-xlarge-up) {
    &__image-background-hover {
      @include t-width-and-height(310);
      margin-top: t-to-rem(-100);
    }
    &__image{
      height: t-to-rem(296);
    } 
    &__inner{
      padding: t-to-rem(90) t-to-rem(38) t-to-rem(100);
    }
  }
}