/*
 *  Header Styles
 *    Styles for the header component.
 *
 *  Version:
 *    1.0.0 - 2021/04/08
 */

 .c-page--product {
  .c-header {
      background: $s-color--aqua;
      color: #000; 
  }
}

.c-header {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: $s-z--header;

  &.is-sticky {
    position: fixed;
    transition: all .5s $s-anim--ease-out;
    top: 0;
    background-color: $s-color--background-tertiary;

    .c-header__inner {
      height: 4.6875rem;
    }

    &.is-scroll-down {
      top: -100%;
    }
  }

  //Inner content, fixed or static
  &__inner {
    display: flex;
    @include t-gutters;
    left: 0;
    justify-content: space-between;
    align-items: flex-start;
    top: 0;
    transition: all $s-anim--time-med $s-anim--ease-out;
    color: $s-color--font-6;
    align-items: center;
    height: t-to-rem(120);

    .has-inverted-header & {
      color: $s-color--font-1;
    }
  }

  &__logo {
    z-index: $s-z--header-logo;
    width: t-to-rem(54);
    width: 54px;
    margin: auto;

    &-icon {
      width: auto;
    }
  }

  &__left,
  &__right {
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__right {
    position: relative;
  }

  //Links in the header, outside of the nav menus
  &__link {
    display: block;
    width: 2rem;

    &.is-mobile-menu {
      svg path {
        stroke: $s-color--white;

        .has-inverted-header & {
          stroke: $s-color--background-secondary;
        }
      }
    }

    //styles for user account 
    &.user-link {
      display: none;
    }

    &.cart-link {
      position: absolute;
      right: 0;
      z-index: $s-z--menu;

      .is-menu-open & {
        z-index: $s-z--cart;
      }

      width: t-to-rem(49);
      height: t-to-rem(49);
      padding: 0;
      border-radius: 50%;
      background-color: $s-color--background;
      transition: background-color $s-anim--time-med $s-anim--ease-out;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: t-to-rem(16);
    }

    .cart-link {
      &__count {
        margin-left: t-to-rem(2);
        color: $s-color--font-1;
        display: none;

        &.has-items {
          display: block;
        }
      }
    }
  }

  //Currency selector
  &__currency {
    display: none;
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-medium-down) {
    &__inner {
      align-items: center;
    }

    &__link {
      cursor: pointer;

      &.is-help,
      &.is-account {
        display: none;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &.is-sticky {
      .c-header__inner {
        height: 6.875rem;
      }
    }

    &__inner {
      height: t-to-rem(164)
    }

    &__logo {
      width: t-to-rem(103);
    }

    &__right {
      display: flex;
      flex: 1;
    }

    &__link {
      padding: 1rem 0.75rem;
      // transition: $s-animation--fast;

      &.is-mobile-menu {
        display: none;
      }

      &:hover {
        text-decoration: none;
        opacity: 0.5;
      }

      &.cart-link {
        top: 50%;
        transform: translateY(-50%);
      }

      &.user-link {
        position: absolute;
        z-index: $s-z--menu;
        right: 56px;
        transition: background-color $s-anim--time-med $s-anim--ease-out;
        width: t-to-rem(49);
        height: t-to-rem(49);
        display: flex;
        align-items: center;
        justify-content: center;

        svg path {
          stroke: $s-color--background;
        }

        &:hover {
          svg path {
            stroke: $s-color--background-secondary !important;
            opacity: 1 !important;
          }
        }
      }
    }
  }
}