/*
 *  Footer Styles
 *    Styles for the Footer component.
 *
 *  Version:
 *    1.0.0 - 2020/02/22
 */

.c-footer {
  background: $s-color--background-secondary;

  &__inner {
    @include t-gutters;
    padding-top: t-to-rem(58);
    padding-bottom: t-to-rem(27);
  }

  &__newsletter {
    &-form-container{
      margin-bottom: t-to-rem(48);
    }

    &-title {
      @include t-font--type-3;
      color: $s-color--white;
      text-align: center;
      margin-bottom: t-to-rem(62);
    }
  
    &__form-form {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid $s-color--font-4;
      @include t-anim--transition(border-color, .5s, ease);
      padding-bottom: t-to-rem(18);
      margin: 0 auto;
      position: relative;

      &::before{
        content: "";
        height: 1px;
        background-color: $s-color--font-1;
        width: 0;
        position: absolute;
        bottom: -1px;
        margin-top: t-to-rem(4);
        left: 0;
        transition: width 1s;
      }
  
      &:hover,
      &:focus-within {
        &::before {
          width: 100%;
        }
      }

      .o-newsletter-form__success {
        text-align: center;
      }
    }

    &__input{
      @include t-font--type-2;
      width: 100%;
      color: $s-color--font-6;
      &::placeholder{
        color: rgba($s-color--font-6, .5);
      }
    }
    &__submit {
      width: t-to-rem(13);

      & .hidden {
        display: none;
      }
    }
    &__submit-loader {
      width: 100%;
      & svg {
        animation: rotator 1.4s linear infinite;
        & .path {
          stroke-dasharray: 280;
          stroke-dashoffset: 0;
          transform-origin: center;
          stroke: $s-color--white;
          animation: dash 1.4s ease-in-out infinite;
        }
      }
      @keyframes dash {
        0% {
          stroke-dashoffset: 280;
        }
        50% {
          stroke-dashoffset: 75;
          transform: rotate(135deg);
        }
        100% {
          stroke-dashoffset: 280;
          transform: rotate(450deg);
        }
      }
      @keyframes rotator {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(270deg);
        }
      }
    }
  }


  &__social-area{
    display: flex;
    .o-social-row__link{
      width: 20px;
      height: 20px;
      margin: 1rem 1rem 0 1rem;
    }
  }
  

  &__navigation{
    display: flex;
    flex-wrap: wrap; // so that other links can be on new line
    justify-content: space-between;

  }


  &__menu{
    &-item{
      @include t-font--type-2;
      display: block;
      color: $s-color--white;
      margin-bottom: t-to-rem(12);

    }
  }

  &__upper{
    padding-bottom: t-to-rem(22);
    border-bottom: 1px solid rgba($s-color--white, 0.1);
    margin-bottom: t-to-rem(32);
  }
  



  //Lower Row
  &__lower {
    display: flex;
    justify-content: space-between;
  }

  &__process-link,
  &__copyright {
    @include t-font--type-4;
    color: rgba($s-color--white, 0.3);
    margin: 0; //trump p
  }


  /*==== Media Queries ====*/
  
  @include t-media-query($s-medium-down) {

    //Logo container and SVG from the customiser
    &__logo {
      display: block;
      width: t-to-rem(98);
      margin: 0 auto t-to-rem(52);;
    }

    &__menu{
      &.is-links{
        margin-top: t-to-rem(33);
        display: flex;
        justify-content: space-between;
        width: 100%;

        .c-footer__menu-item{
          font-size: $s-font--type-4-size;
          color: rgba($s-color--white, 0.3);
        }
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__menu{
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 1rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner{
      padding: t-to-rem(118) 0 t-to-rem(38);
    }

    &__upper{
      display: flex;
      justify-content: space-between;
      margin-bottom: t-to-rem(22);
    }

    &__logo{
      width: t-to-rem(115);
      margin-right: 8%;
    }

    &__newsletter{
      &-form-container{
        width: t-to-rem(300);
        order: 3;
      }
      &-title{
        text-align: left;
      }
    }

    &__navigation{
      margin-right: 4%;
      flex: 1;
    }

    &__menu{
      &-item{
        margin-bottom: t-to-rem(14);
      }
    }
  }
  @include t-media-query($s-large-up) {
    &__logo{
      margin-right: 11.8%;
    }
    &__newsletter{
      &-form-container{
        width: t-to-rem(473);
      }
    }
    &__menu {
      column-gap: 2rem;
    }
    &__navigation{
      margin-right: 4.3%;
    }
  }
}
