.o-shipping-progress {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    display: block;
    padding-bottom: 13px;
    color: $s-color--grey-green;
    text-transform: uppercase;
  }

  &__bar {
    width: 8.8125rem;
    height: 0.125rem;
    background: $s-color--font-10;
    position: relative;
    display: inline-block;

    &-inner {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 80%;
      background: $s-color--blue-green;
      transition: all $s-anim--time-short $s-anim--ease-out;
    }
  }

  @include t-media-query($s-small-up) {
    padding: 0 1rem;
    &__title {
      padding-bottom: 1.25rem;
    }

  }

  @include t-media-query($s-medium-up) {
    //flex-direction: row;
    //justify-content: center;
    &__title {
      padding-bottom: 0;
    }
    &__bar {
      width: 100%;
      margin-top: .75rem;
    }
  }
}