/*
 *  Challenge Page
 */

.shopify-challenge {
  &__container {
    padding: t-to-rem(240) 0 t-to-rem(160);
    text-align: center;

    &::before {
      content: '';
      position: absolute;
      height: t-to-rem(130);
      background-color: $s-color--background-tertiary;
      width: 100%;
      top: 0;
      left: 0;
    }

    input.shopify-challenge__button {
      @extend %s-btn--primary;
    }

    .g-recaptcha {
      margin: 1rem 0 2rem;
    }
  }



}
