.c-logo-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.625rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  &__logo {
    width: 5.625rem;
    display: block;
    
    &-icon { width: 100%; height: auto; display: block; }
  }

  @include t-media-query($s-medium-up) {
    padding: 2.6875rem;

    &__logo {
      width: 7.8125rem;
    }
  }
}