//=== Default Type Styles ===//
.o-type--1 {
  @include t-font--type-1;
}

.o-type--2 {
  @include t-font--type-2;
}

.o-type--3 {
  @include t-font--type-3;
}

.o-type--4 {
  @include t-font--type-4;
}

.o-type--5 {
  @include t-font--type-5;
}

.o-type--6 {
  @include t-font--type-5;
}

.o-type--7 {
  @include t-font--type-7;
}


//=== Heading Type Styles ===//
.o-heading--1 {
  @include t-font--heading-1;
}

.o-heading--2 {
  @include t-font--heading-2;
}

.o-heading--3 {
  @include t-font--heading-3;
}

.o-heading--4 {
  @include t-font--heading-4;
}

.o-heading--5 {
  @include t-font--heading-5;
}

.o-heading--6 {
  @include t-font--heading-6;
}

.o-heading--7 {
  @include t-font--heading-7;
}


.o-title-2 {
  @include t-font--title-2;
}

.o-title-3 {
  @include t-font--title-3;
}


//=== Colour Type Styles ===//
.is-color--black {
  color: $s-color--black;
}

.is-color--white {
  color: $s-color--white;
}

.is-color--secondary {
  color: $s-color--font-1;
}

.is-color--tertiary {
  color: $s-color--font-3;
}

.is-color--octonary {
  color: $s-color--font-10;
}

.errors {
  color: $s-color--red
}